.MainTable {
    border-width: 0;
    border-style: solid;
    border-collapse: collapse;
    margin: 0.2em;
    border-spacing: 0;
    width: 1%;
}

.MainTable > thead > tr > th {
    padding: 0.25em;
    border-width: 1px;
    border-style: solid;
    vertical-align: center;
    font-weight: bold;
    width: 1%;
}

.MainTableSpan {
    vertical-align: center;
    background-color: white;
    color: #333;
    border-color: #E1E1E1;
}

.MainTable > tbody > tr > th {
    padding: 0.25em;
    border-width: 1px;
    border-style: solid;
}

.MainTable > tbody > tr > td {
    padding: 0.25em;
    border-width: 1px;
    border-style: solid;
    color: #e1e1e1;
}

.MainTableEpic {
    background-color: #bface5;
}

.MainTableBusiness {
    background-color: #f0eefa;
    color: #333;
    border-color: #E1E1E1;
}

.MainTableBusinessBacklog {
    background-color: #e6e4f0;
    color: #333;
    border-color: #E1E1E1;
}

.MainTableBusinessUpstream {
    background-color: #f5e9f5;
    color: #333;
    border-color: #E1E1E1;
}

.MainTableBusinessDownstream {
    background-color: #f0eefa;
    color: #333;
    border-color: #E1E1E1;
}

.MainTableBusinessReady {
    background-color: #e5e1fa;
    color: #333;
    border-color: #E1E1E1;
}

.MainTableBusinessActive {
    background-color: #b5d9ae;
    white-space: nowrap;
    position: relative;
    padding-left: 1.2em !important;
}

.MainTableBusinessBacklogActive {
    background-color: #758c70;
    white-space: nowrap;
    position: relative;
    padding-left: 1.2em !important;
}

.MainTableBusinessUpstreamActive {
    background-color: #95b38f;
    white-space: nowrap;
    position: relative;
    padding-left: 1.2em !important;
}

.MainTableBusinessDownstreamActive {
    background-color: #b5d9ae;
    white-space: nowrap;
    position: relative;
    padding-left: 1.2em !important;
}

.MainTableBusinessReadyActive {
    background-color: #92d982;
    white-space: nowrap;
    position: relative;
    padding-left: 1.2em !important;
}

.MainTableDev {
    background-color: #f6f6f6;
}

.MainTableDevHeader {
    background-color: #c6d4f0;
    white-space: nowrap;
}

.MainTableDevActive {
    background-color: #c6d4f0;
    white-space: nowrap;
    position: relative;
    padding-left: 1.2em !important;
}

th.MainTableSpace {
    height: 0.5em;
}

.MainTableHeaderTop1 {
    position: sticky;
    top: 0;
    height: 4em;
    z-index: 1;
    background-color: #fff;
}

.MainTableHeaderTop2 {
    position: sticky;
    top: 4.6em;
    height: 4em;
    z-index: 1;
    color: #333;
    border-color: #E1E1E1;
}

.MainTableHeaderTop1Left1 {
    position: sticky;
    top: 0;
    left: 0;
    height: 4em;
    width: 10em;
    z-index: 2;
    background-color: white;
    color: #333;
    border-color: #E1E1E1;
}

.MainTableHeaderTop2Left1 {
    position: sticky;
    top: 4.6em;
    left: 0;
    height: 4em;
    width: 8em;
    z-index: 2;
    background-color: white;
}

.MainTableHeaderTop2Left2 {
    position: sticky;
    top: 4.6em;
    left: 8em;
    height: 4em;
    width: 8em;
    z-index: 2;
    background-color: white;
}

.MainTableHeaderTop2Left3 {
    position: sticky;
    top: 4.6em;
    left: 16em;
    height: 4em;
    width: 8em;
    z-index: 2;
    background-color: white;
}

.MainTableHeaderLeft1 {
    position: sticky;
    left: 0;
    width: 8em;
    z-index: 1;
}

.MainTableHeaderLeft2 {
    position: sticky;
    left: 8em;
    width: 8em;
    z-index: 1;
}

.MainTableHeaderLeft3 {
    position: sticky;
    left: 16em;
    width: 8em;
    z-index: 1;
}

.MainTableLastHeaderColumn {
    border-right-color: #545454 !important;
}

.MainTableBG {
    background-color: white;
}

.MainTableCounterHolder {
    position: relative;
}

.MainTableCounter {
    bottom: 0;
    color: #333;
    font-size: 75%;
    font-weight: 700;
    margin: .2em;
    position: absolute;
    right: 0;
}

.MainTablePriority {
    position: absolute;
    left: 0;
    top: 0;
    width: 1em;
    height: 1em;
    padding-left: 0.2em;
    padding-right: 0.2em;
}

.MainTableIssueTooltipPlaceholder .MainTableIssueTooltip {
    background-color: white;
    color: black;
    border-radius: 0.5em;
    border-width: 1px;
    border-style: solid;
    border-color: #2c3e50;
    z-index: 100;
}

.IssueTooltipKey {
    font-weight: bold;
}

.IssueTooltipSummary {
    font-weight: bold;
}

.IssueTooltipPriority {
    text-align: right;
}

.IssueTooltipPriorityValue {
    font-weight: bold;
    text-align: left;
}

.IssueTooltipStatus {
    text-align: right;
}

.IssueTooltipStatusValue {
    font-weight: bold;
    text-align: left;
}

.IssueTooltipStatusTime {
    font-weight: normal;
}

.IssueTooltipAssignee {
    text-align: right;
}

.IssueTooltipAssigneeValue {
    font-weight: bold;
    text-align: left;
}

.IssueTooltipBlocks {
    text-align: right;
}

.IssueTooltipBlocksValue {
    font-weight: normal;
    text-align: left;
}

.MainTableBlockedIssue {
    background-color: rgba(255, 64, 64, 0.75) !important;
}

a:link {
    color: #333;
    font-weight: 700;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

a:visited {
    color: #333;
}
