.ConfigEditorTitle {
    font-weight: bold;
}

.ConfigEditorBusinessTable {
    border-collapse: collapse;
    border-style: solid;
    border-width: 1px;
}

.ConfigEditorBusinessTable td {
    border-collapse: collapse;
    border-style: solid;
    border-width: 1px;
    padding: 0.5em;
}

.ConfigEditorDevTable {
    border-collapse: collapse;
    border-style: solid;
    border-width: 1px;
}

.ConfigEditorDevTable td {
    border-collapse: collapse;
    border-style: solid;
    border-width: 1px;
    padding: 0.5em;
}

.ConfigEditorLinkTable {
    border-collapse: collapse;
    border-style: solid;
    border-width: 1px;
}

.ConfigEditorLinkTable td {
    border-collapse: collapse;
    border-style: solid;
    border-width: 1px;
    padding: 0.5em;
}

.ConfigEditorPart {
    padding: 1em;
    float: left;
    display: inline-block;
}

.ConfigEditorBlockSpace {
    max-height: 0.5em;
}

.ConfigEditorBlock {
    clear: left;
    padding: 1em;
    border-color: #E1E1E1;
    border-style: solid;
    border-width: 1px;
    border-radius: 0.5em;
    height: auto;
    overflow: auto;
    margin: 1em;
    text-align: left;
}

.ConfigEditorBlockName {
    text-align: left;
    font-weight: bold;
    padding-bottom: 1em;
    clear: both;
}

.ConfigEditorLinkTableSelected {
    background-color: orange;
}